<script>
  import { onMount } from "svelte";
  import CodeMirror from "../components/CodeMirror.svelte";
  import SimpleAutoComplete from "../components/SimpleAutoComplete.svelte";
  import { state } from "../stores/state.js";
  import { theme } from "../stores/theme.js";
  import { showScripts } from "../stores/showScripts.js";
  import { showTemplates } from "../stores/showTemplates.js";
  import { scriptEditor } from "../stores/scriptEditor.js";
  import { scripts } from "../stores/scripts.js";
  import { termscripts } from "../stores/termscripts.js";

  let editorConfig = {
    language: "javascript",
    lineNumbers: true,
    lineWrapping: true,
    lineHighlight: true,
  };
  let initFinished = false;
  let script;
  let scriptSel = "";
  let scriptName = "";
  let description = "";
  let insert = false;
  let termscript = false;
  let list;

  onMount(() => {
    //
    // Load everything for working with the scripts
    //
    getUserScripts(() => {});
    initFinished = true;
  });

  function getUserScripts(callback) {
    fetch("http://localhost:9978/api/scripts/user", {
      method: "GET",
      headers: {
        "Content-type": "application/json",
      },
    })
      .then((resp) => {
        return resp.json();
      })
      .then((data) => {
        list = data.scripts.sort();
        if (typeof callback !== "undefined") callback();
      });
  }

  function getScript(name, callback) {
    if (name !== undefined && name !== "") {
      fetch(`http://localhost:9978/api/scripts/${name}`, {
        method: "GET",
        headers: {
          "Content-type": "application/json",
        },
      })
        .then((resp) => {
          return resp.json();
        })
        .then((data) => {
          scriptName = data.script.name;
          script = data.script.script;
          description = data.script.description;
          insert = data.script.insert == "true" ? true : false;
          if (typeof data.script.termscript === "undefined") {
            termscript = false;
          } else {
            termscript = data.script.termscript;
          }
          $scriptEditor.setValue(script);
          if (typeof callback !== "undefined") callback();
        });
    }
  }

  function saveScript() {
    if (scriptName !== undefined && scriptName !== "") {
      fetch(`http://localhost:9978/api/scripts/${scriptName}`, {
        method: "PUT",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify({
          script: {
            name: scriptName,
            insert: insert,
            description: description,
            script: script,
            termscript: termscript,
            help: description,
          },
        }),
      }).then(() => {
        scriptSel = "";
        scriptName = "";
        insert = false;
        description = "";
        script = "";
        termscript = false;
        $scriptEditor.setValue(script);
        getScriptsList();
        getUserScripts();
        getTermScriptsList();
      });
    }
  }

  function getScriptsList(callback) {
    fetch("http://localhost:9978/api/scripts/list", {
      method: "GET",
      headers: {
        "Content-type": "application/json",
      },
    })
      .then((resp) => {
        return resp.json();
      })
      .then((data) => {
        $scripts = data.data;
        if (typeof callback !== "undefined") callback();
      });
  }

  function getTermScriptsList(callback) {
    fetch("http://localhost:9978/api/scripts/term/list", {
      method: "GET",
      headers: {
        "Content-type": "application/json",
      },
    })
      .then((resp) => {
        return resp.json();
      })
      .then((data) => {
        $termscripts = data.data;
        if (typeof callback !== "undefined") callback();
      });
  }

  function deleteScript() {
    if (scriptName !== undefined && scriptName !== "") {
      fetch(`http://localhost:9978/api/scripts/${scriptName}`, {
        method: "DELETE",
        headers: {
          "Content-type": "application/json",
        },
      }).then(() => {
        scriptName = "";
        scriptSel = "";
        insert = true;
        description = "";
        script = "";
        $scriptEditor.setValue(script);
        getScriptsList();
        getUserScripts();
        getTermScriptsList();
      });
    }
  }

  function editorChange(e) {
    $scriptEditor = e;
  }

  function textChanged(textCursor) {
    script = textCursor.value;
  }

  function focus() {
    if ($scriptEditor !== null) {
      $scriptEditor.focus();
    }
  }

  function viewNotes() {
    $state = "notes";
  }

  function viewScriptsMenu() {
    $showScripts = !$showScripts;
  }

  function viewTemplateMenu() {
    $showTemplates = !$showTemplates;
  }

  function viewScriptTerm() {
    $state = "scriptterm";
  }

  function changeName(newName) {
    if (newName !== undefined && newName !== "") {
      scriptName = newName;
      getScript(scriptName);
    }
  }
</script>

<div
  id="scripts"
  style="background-color: {$theme.backgroundColor}; font-family: {$theme.font}; color: {$theme.textColor}; font-size: {$theme.fontSize};"
>
  <div id="header">
    <div class="headerRow">
      <label for="scriptName"> Name: </label>
      <SimpleAutoComplete
        inputId="scriptName"
        items={list}
        bind:selectedItem={scriptSel}
        inputClassName="scriptInput"
        className="scriptDiv"
        create="true"
        theme={$theme}
        onChange={changeName}
        onCreate={(name) => {
          if (name !== undefined && name !== "") {
            scriptName = name;
            description = "";
            script = "";
            $scriptEditor.setValue(script);
          }
        }}
      />
      <label id="insertChkLab" for="insertChk"> Insert? </label>
      <input
        id="insertChk"
        name="insertChk"
        type="checkbox"
        bind:checked={insert}
        style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
      />
    </div>
    <div class="headerRow">
      <label for="description"> Description: </label>
      <input
        id="description"
        bind:value={description}
        style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor}; font-size: {$theme.fontSize}"
      />
      <label id="termScriptLab" for="termScriptChk"> Terminal Script? </label>
      <input
        id="termScriptChk"
        name="termScriptChk"
        type="checkbox"
        bind:checked={termscript}
        style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor}; font-size: {$theme.fontSize}"
      />
    </div>
  </div>
  <CodeMirror
    height="380px"
    width="980px"
    config={editorConfig}
    {initFinished}
    on:textChange={(event) => {
      textChanged(event.detail.data);
    }}
    on:editorChange={(event) => {
      editorChange(event.detail.data);
    }}
  />
  <div id="buttonRow">
    <button
      on:click={saveScript}
      style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
    >
      Save Script
    </button>
    <button
      on:click={deleteScript}
      style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
    >
      Delete Script
    </button>
    <button
      on:click={viewNotes}
      style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
    >
      Notes
    </button>
    <button
      on:click={viewScriptTerm}
      style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
    >
      Script Terminal
    </button>
    <button
      on:click={viewScriptsMenu}
      style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
    >
      Scripts Menu
    </button>
    <button
      on:click={viewTemplateMenu}
      style="background-color: {$theme.textAreaColor}; color: {$theme.textColor}; border-color: {$theme.borderColor};"
    >
      Templates Menu
    </button>
  </div>
</div>

<style>
  #scripts {
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin: 0px 10px 10px 10px;
    height: 100%;
    width: 100%;
  }

  #header {
    display: flex;
    flex-direction: column;
    margin: 0px 0px 0px 0px;
    width: 100%;
  }

  #description {
    width: 620px;
    border: solid 1px transparent;
    border-radius: 10px;
    padding: 5px 11px;
    height: 37px;
  }

  #insertChkLab,
  #termScriptLab {
    width: 100px;
    margin: auto 5px auto 10px;
  }

  #insertChk,
  #termScriptChk {
    margin: auto 0px auto 5px;
  }

  :global(.scriptDiv) {
    width: 640px;
  }

  :global(.scriptInput) {
    border-radius: 10px;
  }

  .headerRow {
    display: flex;
    flex-direction: row;
    max-height: 40px;
    margin: 0px 0px 20px 0px;
    width: 100%;
  }

  .headerRow label {
    display: grid;
    justify-content: right;
    margin: auto 20px auto 15px;
    width: 155px;
  }

  #buttonRow {
    display: flex;
    flex-direction: row;
    margin: 10px auto;
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 40px;
  }

  #buttonRow button {
    border-radius: 10px;
    padding: 5px 10px 5px 10px;
    margin: 0px 5px;
    max-height: 40px;
    height: 40px;
    cursor: pointer;
  }
</style>
